@media only screen and (max-width: 767px) {
    /* For Tab */
    .cardEdit {
        margin-bottom: 10px;
    }

    .cardPrcing {
        width: 20rem !important;
    }

}

@media only screen and (max-width: 480px) {
    /* For Mobile */
    .cardEdit {
        margin-bottom: 10px;
    }

    #homeTitleContainer {
        padding-bottom: 30px !important;
    }

    #homeTitle {
        font-size: 30px;
    }

    .cardPrcing {
        width: 20rem !important;
    }

    #footerContainer{
        padding-top: 20px;
    }

    #contactText {
        font-size: 30px;
    }

    #bottomFooterInfo {
        font-size: 12px;
    }
}